import React, { Component } from "react";
import RoomChoice from "../components/RoomChoice";
import TimeSelect from "../components/TimeSelect";
import { connect } from "react-redux";
import { fetchAllSchedules, sendPost } from "../actions/api_actions";
import {
  toggleFormVisible,
  toggleRoomCheck,
  changeFormValue
} from "../actions/form_actions";

class PostForm extends Component {
  targetDateTime = () =>{
    return `${this.props.targetYear}-${this.props.targetMonth}-${
    this.props.targetDate
  }`
}
  backAction = e => {
    e.preventDefault();
    this.props.toggleFormVisible();
    this.props.fetchAllSchedules(
      this.targetDateTime()
    );
  };
  registAction = e => {
    e.preventDefault();
    const promise = new Promise((resolve, reject) => {
      this.props.sendPost(this.props.insertSchedule);
      resolve();
    });
    promise.then(() => {
      this.props.toggleFormVisible();
      this.props.fetchAllSchedules(
        this.targetDateTime()
      );
      setTimeout(() => {
        this.props.fetchAllSchedules(
          this.targetDateTime()
        );
      }, 2000);
    });
  };
  changeFormValueAction = e => {
    this.props.changeFormValue(e.target.name, e.target.value);
  };
  componentWillMount() {
    this.changeFormValueAction = this.changeFormValueAction.bind(this);
  }
  render() {
    const schedule = this.props.insertSchedule;
    // formVisibleの値によって適用
    const formStyle = { display: "none" };
    if (this.props.formVisible) {
      formStyle.display = "flex";
    }
    return (
      <div className="box-postForm" style={formStyle}>
        <form className="form-simple-01">
          <div class="flex-row-90">
            <TimeSelect
              name="startTime"
              initialValue={schedule.startTime.split(" ")[1]}
              timeList={this.props.timeList}
              onChange={this.props.changeFormValue}
              targetDate={this.targetDateTime()}
            />
            から
            <TimeSelect
              name="endTime"
              initialValue={schedule.endTime.split(" ")[1]}
              timeList={this.props.timeList}
              onChange={this.props.changeFormValue}
              targetDate={this.targetDateTime()}
            />
            まで
          </div>
          <div className="flex-row-90">
            <RoomChoice
              roomIds={schedule.roomIds}
              roomCheckAction={this.props.toggleRoomCheck}
            />
          </div>
          <div className="flex-row-90">
            <input
              type="text"
              className="input-text-01"
              placeholder="名前を入力してください"
              name="userName"
              onChange={this.changeFormValueAction}
              value={schedule.userName}
            />
          </div>
          <div className="flex-row-90">
            <textarea
              placeholder="内容を入力してください"
              className="input-text-02"
              name="content"
              onChange={this.changeFormValueAction}
              value={schedule.content}
            />
          </div>
          <div className="flex-end-90">
            <button onClick={this.backAction} className="btn-simple-01">
              戻る
            </button>
            <button onClick={this.registAction} className="btn-simple-03">
              登録
            </button>
          </div>
        </form>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  formVisible: state.form.formVisible,
  insertSchedule: state.form.insertSchedule,
  roomSelected: {
    1: state.form.roomSelected[1],
    2: state.form.roomSelected[2],
    3: state.form.roomSelected[3],
    4: state.form.roomSelected[4],
    5: state.form.roomSelected[5],
    6: state.form.roomSelected[6],
    7: state.form.roomSelected[7],
    8: state.form.roomSelected[8]
  },
  targetYear: state.general.targetYear,
  targetMonth: state.general.targetMonth,
  targetDate: state.general.targetDate
});
export default connect(
  mapStateToProps,
  {
    toggleFormVisible,
    changeFormValue,
    sendPost,
    toggleRoomCheck,
    fetchAllSchedules
  }
)(PostForm);
