import React from "react";



const RoomChoice =(props)=>{
    const roomChoiceArray = {
        1:"会議室1",
        2:"会議室2",
        3:"会議室3",
        4:"会議室4",
        5:"会議室5",
        6:"会議室6",
        7:"会議室7",
        8:"会議室8",

}
const choiceElements = []
for(let i in roomChoiceArray){
    if(props.roomIds.indexOf(Number(i))>=0){
    choiceElements.push(
        <div class="box-roomChoice">
        <input type="checkbox" className="select-input-01-checked" name="room-choice" id={`room-choice-${i}`}   onClick={(e)=>{props.roomCheckAction(i)}} value={i} checked/>
        <label  className="select-label-01-checked" onClick={(e)=>{props.roomCheckAction(i)}}>{roomChoiceArray[i]}</label>
        </div>
    )
    }else{
        choiceElements.push(
        <div class="box-roomChoice">
        <input type="checkbox" className="select-input-01" name="room-choice" id={`room-choice-${i}`} value={i}   onClick={(e)=>{props.roomCheckAction(i)}}/>
        <label className="select-label-01" onClick={(e)=>{props.roomCheckAction(i)}}>{roomChoiceArray[i]}</label>
        </div>)
    }

}
    return(
        <div class="flex-row-100">
            {choiceElements.map((elem)=>{
                return(elem);
            })}
        </div>
    )
}

export default RoomChoice;