import React from "react";

const TimeSelect = props => {
  return (<select name={`time-select-${props.name}`} class="time-select-01" onChange={(e)=>{props.onChange(props.name,props.targetDate + " " + e.target.value+":00")}}>
  {props.timeList.map((time, index)=>{
      if(props.initialValue==`${time}:00`){
      return(<option value={time} selected class="time-option-01-selected">{time}</option>)}
      else{
        return(<option value={time} class="time-option-01">{time}</option>)
      }
  })}</select>);
};

export default TimeSelect;